.quantity-main-container{
	margin: 20px auto;
	position: relative;
	display: flex;
	width: 100%;
	flex-wrap: wrap;
	box-sizing: border-box;
	max-width: 375px;	
	font-size: 24px;
}

.quantity-option {
	flex: 1 1 40%;
    justify-content: space-evenly;
    margin: 10px;
    border: 1px solid rgba(0,0,0,0.2);
    border-radius: 4px;
    padding: 10px 0;
    cursor: pointer;
    color: rgba(0,0,0,0.6);
}

