:root {
	--red-position-x: 0%;
	--red-position-y: 0%;
}

.ludo-container {
	position: relative;
}

.board-container {
	margin: 20px auto;
	position: relative;
	display: flex;
	width: 100%;
	flex-wrap: wrap;
	box-sizing: border-box;
	border-left: 1px solid black;
	border-top: 1px solid black;
	max-width: 375px;	
	font-size: 12px;
}

.board-cell {
	width: 10%;
	box-sizing: border-box;
	border-right: 1px solid black;
	border-bottom: 1px solid black;
    padding: 10px;
    text-align: center;
}

.goti {
	width: 25px; 
	height: 25px;
	border-radius: 100%;
	position: absolute;
    bottom: var(--red-position-y);
    left: var(--red-position-x);
    transform: translate(25%, -25%);
    transition: all 0.1s;
    z-index: 1;
}

.goti.not-started {
	bottom: -8%;	
    transform: scale(0.6);
}

.goti.red.not-started {
    left: -4px; 
}
.goti.blue.not-started {
    left: 2px; 
}
.goti.green.not-started {
    left: 8px;
}
.goti.aqua.not-started {
    left: 14px;
}

.goti::after {
    content: "";
    position: absolute;
    width: 10px;
    height: 10px;
    background: white;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    border-radius: 50%;
}

.goti.red {
	background-color: red;
}
.goti.green {
    background-color: green;
}
.goti.blue {
    background-color: blue;
}
.goti.aqua {
    background-color: aqua;
}



