.dice-container{
    display: flex;
    display: inline-flex;
    margin: 0 auto;
    align-items: center;
    padding: 2px;
    border: 1px solid black;
    border-radius: 3px;
}

.dice-container.red{
    border-color: red;
}
.dice-container.green{
    border-color: green;
}
.dice-container.blue{
    border-color: blue;
}   
.dice-container.aqua{
    border-color: aqua;
}   

.dice-container .goti-preview {
    width: 20px; 
    height: 20px;
    border-radius: 100%;
    background-color: black;
    position: relative;
    margin-left: 3px;
}

.dice-container.red .goti-preview {
    background-color: red;
}
.dice-container.green .goti-preview {
    background-color: green;
}
.dice-container.blue .goti-preview {
    background-color: blue;
}
.dice-container.aqua .goti-preview {
    background-color: aqua;
}

.dice-container .player-name {
    text-transform: capitalize;
    max-width: 100px;
    margin-right: 15px;
    overflow: hidden;
    margin-left: 5px;
    text-align: left;
    text-overflow: ellipsis;
    white-space: nowrap;
}

.dice-container.red .player-name {
    color: red;
}
.dice-container.green .player-name {
    color: green;
}
.dice-container.blue .player-name {
    color: blue;
}
.dice-container.aqua .player-name {
    color: aqua;
}

.dice-container .goti-preview::after {
    content: "";
    position: absolute;
    width: 10px;
    height: 10px;
    background: white;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    border-radius: 50%;
}

.dice-container .dice {
    font-size: 16px;
    font-weight: 600;
    color: rgba(0,0,0,0.6);
    border: 1px solid rgba(0,0,0,0.8);
    width: 30px;
    height: 30px;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 3px;
    cursor: pointer;
}

.dice-container .dice:hover {
    transform: translateY(-1px);
    box-shadow: 0 1px 1px 0 rgba(0,0,0,0.6);
}

.dice-container .dice:hover.disable{
    transform: translateY(0);
    box-shadow: none;
}

.dice-container.red .dice.disable {
    color: red;
    border-color: red;
}
.dice-container.green .dice.disable {
    color: green;
    border-color: green;
}
.dice-container.blue .dice.disable {
    color: blue;
    border-color: blue;
}
.dice-container.aqua .dice.disable {
    color: aqua;
    border-color: aqua;
}

/*loader*/
.lds-hourglass {
  display: inline-block;
  position: relative;
  width: 24px;
  height: 24px;
}
.lds-hourglass:after {
  content: " ";
  display: block;
  border-radius: 50%;
  width: 0;
  height: 0;
  margin: 2px;
  box-sizing: border-box;
  border: 10px solid rgba(0,0,0,0.7);
  border-color: rgba(0,0,0,0.7) transparent rgba(0,0,0,0.7) transparent;
  animation: lds-hourglass 1.2s infinite;
}
@keyframes lds-hourglass {
  0% {
    transform: rotate(0);
    animation-timing-function: cubic-bezier(0.55, 0.055, 0.675, 0.19);
  }
  50% {
    transform: rotate(900deg);
    animation-timing-function: cubic-bezier(0.215, 0.61, 0.355, 1);
  }
  100% {
    transform: rotate(1800deg);
  }
}
