.single-input-container{
	display: flex;
	margin-bottom: 20px;
}

.single-input-container::before {
	content: "";
    position: absolute;
    width: 20px;
    height: 20px;
    background: red;
    border-radius: 50%;
    display: flex;
    align-items: center;
    border: 1px solid rgba(0,0,0,0.3);
}

.single-input-container.red::before{
	background: red;
}
.single-input-container.green::before{
	background: green;
}
.single-input-container.blue::before{
	background: blue;
}
.single-input-container.aqua::before{
	background: aqua;
}

.single-input-container input {
	width: 100%;
    margin-left: 30px;
    border-color: red;
    border: 1px solid red;
}

.single-input-container button {
	font-size: 14px;
    background: none;
    border-color: red;
    color: red;
    text-transform: capitalize;
    margin-left: 10px;
    border-radius: 3px;
}

.single-input-container input.red, button.red{
	border-color: red;
	color: red;
}
.single-input-container input.green, button.green{
	border-color: green;
	color: green;
}
.single-input-container input.blue, button.blue{
	border-color: blue;
	color: blue;
}
.single-input-container input.aqua, button.aqua{
	border-color: aqua;
	color: aqua;
}
